.Footer{
    background: #333333;
    color: white;
    flex-shrink: 0;
}
.footerInner{
    height: 115px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.FooterLogoSection{
    width: 400px;

}
.FooterLogoSection img{
    display: inline-block;
}
.FooterLogoSection span{
    display: inline-block;
    width: 190px;
    padding-left: 13px;
    font-size: 14px;
    border-left: 1px solid white;
    margin-left: 15px;
}
.FooterNavigationSection ul{
    list-style: none;
}
.FooterNavigationSection ul li{
    display: inline-block;
    margin: 0 10px;
   
}
.FooterNavigationSection ul li a{
    color: white;
    text-decoration: none;
}
.FooterAuthSection .exitLink{
    border: 2px solid #f63e3e;
    background: transparent;
    color: #f63e3e;
    padding: 15px 40px;
    border-radius: 3px;
    margin-left: 20px;
    transition: 0.5s all ease-in-out;
}
.FooterAuthSection .exitLink:hover{
    background: #f63e3e;
    color: white;
    cursor: pointer;
}