        padding: 10px 40px;
        align-items: center;
        justify-content: space-between;
        margin: 5px 0;
        height: 180px;

}
.AdsPhoto{
    width: 300px;
    max-width: 100%;
    height: 100%;
}
.AdsPhoto img{
  max-height: 100%;
}
.AdsInfo{
    width: 650px;
    max-width: 100%;
    text-align: left;
    padding-left: 50px;
}
.AdsInfo h3{
    font-size: 17px;
    font-family: 'Geometria-medium', sans-serif;
}
.AdsInfo p{
    font-size: 15px;
    font-family: 'Geometria-light', sans-serif;
    line-height: 14px;
}
img{
    max-width: 100%;
}
.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 24px;
    margin-left: 5px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {display:none;}
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 20px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #f63e3e;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #f63e3e;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(22px);
    -ms-transform: translateX(22px);
    transform: translateX(22px);
  }
  .activeBox{
      display: flex;
  }