.Authorization{
    height: 100vh;
    display: flex;
    justify-content: center;
    padding-top: 100px;
    flex-grow: 1;
    width: 100%;
    background: url('../../background.jpg') no-repeat;
    background-size: cover;

}
.Authorization >div{
   width: 100%;
   max-width: 600px;
    padding: 0 20px;
}
.logo{
    margin: 0 auto;
    display: block;
}
h1{
    color: #fff;
    text-align: center;
}

.AuthForm{
    background: #eee;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, .5);
    padding: 15px;
    border-radius: 5px;
}
.AuthForm button{
    margin: 0 auto;
    display: block;
}
.changeType{
    text-align: center;
    margin-top: 20px;
    background: #ffffffb8;
    padding: 5px;
}
.changeType button{
    background: none;
    border: 0;
    text-decoration: underline;
    cursor: pointer;

}
.errorMessage{
    text-align: center;
    margin: 5px 0;
    color: red;
}