.About{

}
.shopAbout{
text-align: left;
}
.shopAbout label{
    display: block;
    font-family: "Geometria-medium", sans-serif;
    margin-bottom: 8px;
}
.shopAbout input[type=text], textarea{
    width: 100%;
    padding: 20px;
    color: #776f6f;
    border: 1px solid #e6e6e6;
    font-size: 15px;
}
.formGroup{
    margin-bottom: 30px;
}
.shopAbout input[type=submit]{
    padding: 20px 50px;
    background: #f63e3e;
    border-radius: 3px;
    color: white;
    border: none;
    text-align: center;
    margin: 0 auto;
    display: block;
}
.shopAbout input[type=submit]:hover{
   cursor: pointer; 
}
.imageLogo{
    max-width: 300px;
}
.tinkoffPayRow{display:block;margin:1%;width:160px;}
.statusBlock{
    border: 1px solid grey;
    margin: 10px 0;
    display: block;
    padding: 0 20px;
    font-size: 21px;
}
.statusBlock a{
    color: #f63e3e;
    text-decoration: none;
}
.payLink{
    border: 2px solid #f63e3e;
    background: #f63e3e;
    color: white;
    padding: 15px 40px;
    border-radius: 3px;
    margin-top: 20px;
    transition: 0.5s all ease-in-out;
    text-decoration: none;
}
.payLink:hover{
    cursor: pointer;
    background: white;
    color: #f63e3e;
}
.checkBoxes{
    display: flex;
    flex-direction: column;
    justify-content: left;
}
.checkBoxes p{
    text-align: left;
}
textarea.textarea{
    resize: none; 
    width: 1057px;
}
.error{
    color: red;
    border: 1px solid red;
    padding:5px 10px;
}
.leftInfo{
    width: 350px;
    max-width: 100%;
    display: inline-block;
    padding: 10px 0;
    margin-right: 50px;
}
.rightInfo{
    display: inline-block;
    width: 603px;
    padding: 24px 35px;
    max-width: 100%;
    border: 2px solid #838282;
}
.label{
    font-family: "Geometria-medium", sans-serif;
    font-weight: bold;
}
.infoTopBlock{
    display: flex;
    align-items: flex-start;
}