.modalOverlay{
    margin: 0; /* убираем отступы */
    padding: 0; /* убираем отступы */
    position: fixed; /* фиксируем положение */
    top:0; /* растягиваем блок по всему экрану */
    bottom:0;
    left:0;
    right:0;
    background: rgba(0,0,0,0.5); /* полупрозрачный цвет фона */
    z-index:100; /* выводим фон поверх всех слоев на странице браузера */ /* Делаем невидимым */
 }
.modalWindow{
    width: 100%;
    max-width: 300px;
    margin: 10% auto;
    padding: 20px;
    background: #fff;
    border-radius: 3px;
    position: relative;
}
.modalBody{
    margin: 20px 0;
    text-align: center;
}
.modalTitle{
    display: flex;
    justify-content: center;
    border-bottom: 1px dotted #f63e3e;
    padding-bottom: 19px;
    font-size: 22px;
}
.modalTitle a{
    color: #ffffff;
    position: absolute;
    right: -16px;
    top: -14px;
    padding: 1px;
    background: #f63e3e;
    border-radius: 31px;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 17px;
    text-decoration: none;
    font-weight: 100;
}