.Header{
}
.logoSection{
    width: 210px;
}
.logoSection span{
    display: block;
}
.headerContainer{
    height: 120px;
}
.navigationSection ul{
    list-style: none;
    font-family: "Geometria-medium", sans-serif;
}
.navigationSection ul li{
    display: inline-block;
    margin: 0 10px;
   
}
.navigationSection ul li a{
    color: #474747;
    text-decoration: none;
}
.navigationSection a.active{
    color: #f63e3e;
}
.authSection{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.authSection span{
    font-weight: 600;
}
.authSection .exitLink{
    border: 2px solid #f63e3e;
    background: transparent;
    color: #f63e3e;
    padding: 15px 40px;
    border-radius: 3px;
    margin-left: 20px;
    transition: 0.5s all ease-in-out;
    text-decoration: none;
}
.authSection .exitLink:hover{
    background: #f63e3e;
    color: white;
    cursor: pointer;
}
.headerContainer{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px;
}
.headerBalance{
    font-size: 14px
}
.headerBalance a{
    color: #f63e3e
}
.payLink{
    border: 2px solid #f63e3e;
    background: transparent;
    color: #f63e3e;
    padding: 15px 40px;
    border-radius: 3px;
    margin-left: 20px;
    transition: 0.5s all ease-in-out;
    text-decoration: none;
}