.avtoType{
    display: flex;
    justify-content: left;
    text-align: left;
}
.avtoType>div{
    width: 100%;
    margin: 3px
}
.createButton{
    padding: 20px 50px;
    background: #f63e3e;
    border-radius: 3px;
    color: white;
    border: none;
    text-align: center;
    margin: 20px auto;
    display: block;
}

.createButton:hover{
    cursor: pointer
}
.createButton:disabled{
   
        background: #ccc;
        color: #000;
        cursor: not-allowed;
  
}
.addAdsBlock{
    width: 685px;
    max-width: 100%;
    display: inline-block;
}
.downloadAdsBlock{
    width: 380px;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    float: right;
    border-left: 1px solid;
    height: 326px;
    padding: 0 42px;
    box-sizing: border-box;
}
.infoLinks{
    font-size: 14px;
    padding: 10px 0;
    display: block;
}