.pageLink{

    cursor: auto;
}


.pageLink {
        position: relative;
        display: block;
        padding: .5rem .75rem;
        margin-left: -1px;
        line-height: 1.25;
        color: #007bff;
        background-color: #fff;
        border: 1px solid #dee2e6;
    }

.pagination {
        display: -ms-flexbox;
        display: flex;
        padding-left: 0;
        list-style: none;
        border-radius: .25rem;
        justify-content: left;
        flex-wrap: wrap;
    }

    .pageItem:first-child .pageLink {
        margin-left: 0;
        border-top-left-radius: .25rem;
        border-bottom-left-radius: .25rem;
    }
    .pagination a:first-child {
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }
    .pagination a {
        color: black;
        float: left;
        padding: 8px 16px;
        text-decoration: none;
        border: 1px solid #ddd;
        width: 18px;
    margin: 1px;
    }
    .pagination a:hover {
      cursor: pointer;
      background-color: #eb8f8f;
    }
    .pagination a.active {
        background-color: #f63e3e;
        color: white;
        border-radius: 5px;
    }